<template>
  <div :class="{'blurx': loading}" class="home">
    <div class="con-inputs">
      <h1>BTC - USD - VES</h1>

      <vs-input :disabled="result !== ''" label-placeholder="Valor BTC en VES" v-model="priceBs" type="number"/>
      <!-- <input placeholder="valor del BTC en Dolares" v-model="priceDolar" type="number"> -->

      <!-- <button @click="getValue">Obtener resultado</button> -->
      <vs-button v-if="!result" :disabled="!priceBs" @click="getValue" type="gradient">Obtener Valor de 1$ en VES</vs-button>
      <vs-button class="btnw" v-else icon="refresh" color="success" :disabled="!priceBs" @click="refresh" type="gradient">
      </vs-button>

      <!-- <button @click="getVal"></button> -->
      
      <vs-input class="inputx" readonly placeholder="Resultado" v-model="result" type="number"/>
      <!-- <input class="readonly" placeholder="resultado" readonly v-model="result" type="text"> -->
      
      <vs-alert 
        color="warning"
        closable
        close-icon="close" 
        :active.sync="activeInfo" title="Info">
        <p class="px">
          VES({{ priceBs }}) / BTC({{ btcDolar }}) = <b>{{ result }}</b>
        </p>
      </vs-alert>

      <vs-button v-if="result" :class="{'noVisible': activeInfo}" @click="activeInfo = !activeInfo" color="warning" type="border" icon="live_help"></vs-button>
    </div>

    <div id="codefund"></div>
  </div>
</template>

<script>
export default {
  name: 'home',
  mounted() {
    let recaptchaScript = document.createElement('script')
    recaptchaScript.setAttribute('src', 'https://codefund.app/properties/160/funder.js')
    recaptchaScript.setAttribute('async', 'async')
    document.head.appendChild(recaptchaScript)
  },
  data: () => ({
    result: '',
    priceDolar: '',
    priceBs: '',
    btcDolar: '',
    loading: false,
    activeInfo: false
  }),
  watch: {
    loading() {
      this.activeAlert = (this.btcDolar != '')
    }
  },
  methods:{
    refresh() {
      this.priceBs = ''
      this.btcDolar = ''
      this.result = ''
    },
    getValue () {
      this.loading = true
      this.$vs.loading({
        type: 'corners',
        background: 'rgba(255,255,255,.7)'
      })

      let _this = this
      let btcDolar = 0

      fetch('https://api.coindesk.com/v1/bpi/currentprice.json')
        .then(function(response) {
          return response.json();
        })
        .then(function(myJson) {
          btcDolar = myJson.bpi.USD.rate
          btcDolar = btcDolar.replace(',','').replace('.','')
          _this.priceDolar = parseInt(btcDolar)
          console.log('myJson>>>>>>', myJson)
          console.log('priceBs>>>>>>', parseInt(_this.priceBs))
          let res = parseInt(_this.priceBs) / _this.priceDolar 
          console.log(res)
          _this.result = (res * 10000).toFixed(2) 
          _this.btcDolar = myJson.bpi.USD.rate
          _this.loading = false
          _this.$vs.loading.close()
        });     
     
    }
  }
}
</script>
